import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '../../components/common/upload-images.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';

export default {
	components: {
		uniNavBar,
		uploadImages,
		uniPopup,
	},
	data() {
		return {
			isget: false,
			showpopup: true,
			title: "所有人可见",
			titles: ['所有人可见', '仅自己可见'],
			text: "",
			imageList: []
		}
	},
	onLoad() {
		this.$refs.popup.open();
	},
	onBackPress() {
		
		if (!this.text.length && this.imageList.length<1) { return }
		
		if (!this.isget) {
			this.caogao();
			return true;
		}
	},
	methods: {
		caogao() {
			uni.showModal({
				title: '提示',
				content: '是否要保存',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm) {
						
					}
					
					this.isget = true
					uni.navigateBack({
						delta: 1,
					});
				}
			});
		},
		changelook() {
			uni.showActionSheet({
			    itemList: this.titles,
			    success: (res)=> {
					this.title = this.titles[res.tapIndex]
			    }
			});
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		upload(res) {
			console.log(res);
		},
		change() {
			this.$refs.popup.close();
		}
	}
}
	
	
	
